<template>

  <div class="panel">
    
    <div class="page__hd">
      <h4 class="page__title">{{this.meetingTitle}}</h4>
      <p class="page__desc">{{this.meetingType}}</p>
    </div>
    <div class="page__bd">

         <div class="weui-panel weui-panel_access" v-for="item in raterList">

              <div class="weui-panel__bd">

                    <div class="weui-panel weui-panel_access">
                    <div class="weui-panel__bd">
                      <router-link :to="getUrl(item)" class="weui-media-box weui-media-box_appmsg">
                      <div class="weui-media-box__bd">
                        <h4 class="weui-media-box__title">No. {{item.partino}}</h4>
                        <h4 v-if="item.rated == true" class="weui-media-box__title expireSafe">
                            已评价
                        </h4>
                        
                        <h4 v-if="item.rated != true && item.needAlert" class="weui-media-box__title expireAlert">
                            {{item.raterDate}}前需评价
                        </h4>

                        <h4 v-if="item.rated != true && !item.needAlert" class="weui-media-box__title expireSafe">
                            {{item.raterDate}}前需评价
                        </h4>
        
                        <ul class="weui-media-box__info">
                          <li class="weui-media-box__info__meta">摘要：</li>
                        </ul>
                        <p class="weui-media-box__desc">{{item.msg}}</p>
                          
                        
                      </div>

                      </router-link>

                      </div> <!--end weui-panel__bd -->
                    </div>
              </div><!--weui-panel__bd-->
         </div>
    </div> <!-- page__bd-->    
  </div>


</template>

<script>
import server from '../assets/js/server.js'


export default {
 
  name: '',
  props :['raterstate','raterList','meetingTitle','meetingType'],
  data() {
    return {
      badge:this.$store.state.user.badge,
      meetingId:this.$route.query.meetingId,
      ratelevel:this.$route.query.ratelevel,
      search:'false',
    };
  },
  mounted(){
    document.title = '心得列表';
  },

  methods:{

    getPrompt:function(day,hour){
        if(day=='-1')
          return '已评价';
        else if(day=='-2')
          return '评分超期';
        else if(day=='-3')
          return '暂无期限';
        else if(day=='-4')
          return '超期';
        else if(day =='0') {
          return '剩余'+hour+'小时';
        }else
          return '剩余 '+day+' 天';
    },

    getColor:function(day){
      if((day >= 0 && day <= 3) || day == -2)
        return true;
      else
        return false;
    },
    
    getUrl:function(item){
      
      if(item.msg=='评价心得未提交,请等待!') {

        return '#';
      
      } else {
        
        return '/rate?ratelevel='+this.ratelevel+'&raterId='+item.raterId
                +'&participantId='+item.participantId;
      
      }
    },
  },

  computed: {

  }

};
</script>

<style scoped>

  .page__hd{
    padding:30px;
  }
  .sss{
    width:110px;
  }

  .c{
    color:black;
  }
  .page-tabbar {
    overflow: hidden;
    height: 100vh;
  }
  .page-wrap {
    overflow: auto;
    height: 100%;
    padding-bottom: 100px;
  }
  .weui-tabbar__icon {
    display: inline-block;
    width: 27px;
    height: 27px;
  }
  .expireAlert{
    color:red;
  }
  .expireSafe{
    color:green;
  }
.weui-tabbar__label {
    text-align: center;
    color: green;
    font-size: 12px;
    line-height: 1.8;
}
.weui-tabbar__item {
  background-color: #F2F4F3;
}
</style>



